<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        href="#"
        aria-label="Следующая страница"
        @click.prevent="startPagination"
      >
      <i class="fa-solid fa-angles-left"></i>
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow pagination__link--disabled"
        aria-label="Предыдущая страница"
        href="#"
        @click.prevent="previousPage(page)"
      >
        <i class="fa-solid fa-angle-left"></i>
      </a>
    </li>
    <li :class="{ pagination__item: page != 1 }">
      <a
        class="pagination__link"
        v-show="page != 1"
        @click.prevent="paginate(page - 1)"
      >
        {{ page - 1 }}
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--current"
        @click.prevent="paginate(page)"
      >
        {{ page }}
      </a>
    </li>
    <li :class="{ pagination__item: page != amountOfPages }">
      <a
        class="pagination__link"
        v-show="page != amountOfPages"
        @click.prevent="paginate(page + 1)"
      >
        {{ page + 1 }}
      </a>
    </li>

    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        href="#"
        aria-label="Следующая страница"
        @click.prevent="nextPage(page)"
      >
        <i class="fa-solid fa-angle-right"></i>
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        href="#"
        aria-label="Следующая страница"
        @click.prevent="endPagination"
      >
        <i class="fa-solid fa-angles-right"></i>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["page", "count", "perPage"],
  computed: {
    amountOfPages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(value) {
      this.$emit("update:page", value);
    },
    nextPage(page) {
      if (page < this.amountOfPages) {
        this.$emit("update:page", page + 1);
      }
    },
    previousPage(page) {
      if (page > 1) {
        this.$emit("update:page", page - 1);
      }
    },
    endPagination() {
      this.$emit("update:page", this.amountOfPages);
    },
    startPagination() {
      this.$emit("update:page", 1);
    },
  },
};
</script>

<style>
.pagination {
  margin: auto;
  padding-bottom: 5vh;
  display: flex;
  align-content: center;
  justify-content: space-between;
  width: 30vw;
  font-size: 20px;
}
.pagination__item {
  background: var(--light-grey);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.pagination__link--current {
  font-weight: 1000;
  transform: scale(1.1);
}
.pagination__link {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>