<template>
  <div class="flavor">
    <p>Вкус</p>
    <input
      type="text"
      class="searchArea"
      id="input"
      v-model="searchArea"
      placeholder="Поиск"
    />
    <ul>
      <li class="taste-checkbox" v-for="flav in searchFlavors" :key="flav.id">
        <input
          type="checkbox"
          :id="flav.id"
          :value="flav.id"
          v-model="pushFlavors"
        />
        <label :for="flav.id" class="container">
          <i class="fa-solid fa-martini-glass-empty"></i>
          <span class="text">{{ flav.taste }}</span>
        </label>
      </li>
    </ul>
  </div>
</template>

<script>
import flavors from "@/data/flavors";

export default {
  props: ["filteredFlavors"],
  data() {
    return {
      flavors,
      searchArea: "",
    };
  },
  computed: {
    pushFlavors: {
      get() {
        return this.filteredFlavors;
      },
      set(value) {
        this.$emit("update:filteredFlavors", value);
      },
    },
    searchFlavors() {
      return this.flavors.filter((flav) => {
        return flav.taste.toLowerCase().includes(this.searchArea.toLowerCase());
      });
    },
  },
};
</script>

<style>
.searchArea {
  padding: 0 10px;
  border-radius: 10px;
  height: 2rem;
  width: 150px;
  background-color: var(--light-grey);
  color: var(--white-color);
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}
.searchArea::placeholder {
  color: var(--white-color);
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}
</style>