<template>
  <div class="taste-checkbox">
    <input type="button" id="reset" @click="resetAll">
    <label for="reset" class="container reset">
      <i class="fa-solid fa-martini-glass-empty"></i>
      <span class="text">Сбросить<br>фильтр</span>
    </label>
  </div>
</template>

<script>
export default {
  props: ['filterSweet', 'filterSour', 'filterStrength', 'filterBitter', 'filteredFlavors', 'filterAlcohols'],
methods: {
  resetAll() {
    this.$emit('update:filterSweet', false);
    this.$emit('update:filterSour', false);
    this.$emit('update:filterStrength', undefined);
    this.$emit('update:filterBitter', false);
    this.$emit('update:filteredFlavors', []);
    this.$emit('update:filterAlcohols', []);
  },
}
}
</script>

<style>
.reset{
  height: 3rem;
  width: 150px;
  margin: auto;
}
.taste-checkbox input:active ~ .container i{
  color: #0ef65b;
  text-shadow: 0 0 5px #d4e0db;
  border: solid 1px #0ef65b96;
  box-shadow: 0 0 3px #0ef65b;
}
.reset i{
  transition: color 0.1s;
}
</style>