import { createRouter, createWebHashHistory } from "vue-router";
import MainPage from "@/pages/MainPage.vue";
import CocktailPage from '@/pages/CocktailPage.vue';
import MostPopular from '@/pages/MostPopular.vue'
import CalcPage from  '@/pages/CalcPage.vue'
import config from '../../vue.config'

const routes = [
  { name: "main", component: MainPage, path: "/" },
  { name: "cocktail", component: CocktailPage, path: "/cock/:id" },
  { name: "popular", component: MostPopular, path: "/cock/mostPopular" },
  { name: "calculator", component: CalcPage, path: "/cock/calculator" },
];

const router = createRouter({
  history: createWebHashHistory(config.publicPath),
  routes,
});

export default router;
