<template>
  <div class="alcohol-strength">
    <span class="alcohol">
      <p>Крепость</p>
      <p id="rangeValue">Легкий</p>
    </span>
    <input type="range" min="0" max="100" oninput="alcoholStrength(this.value)" v-model.number="strengthChange">
  </div>
  
</template>

<script>
export default {
  data() {
    return {
      alcoholStrength: true,
    }
  },
props: ['strength'],
computed: {
  strengthChange: {
    get() {
      return this.strength
    },
    set(value) {
      this.$emit('update:strength', value)
    },
  }
},
}
</script>

