<template>
  <div>
    <main class="main-catalog">
      <button class="filter-toggler" @click="changeFilterButton" ref="buttonForFilter">
        <i class="fa-solid fa-filter"></i>
      </button>
      <div
        class="sidebar"
        ref="filtersBlock"
        :style="{
          visibility: isItMobile
            ? filterButton
              ? 'visible'
              : 'hidden'
            : 'visible',
        }"
      >
        <FilterTaste
          v-model:sweetCondition="tasteFilterSweet"
          v-model:sourCondition="tasteFilterSour"
          v-model:bitterCondition="tasteFilterBitter"
        />
        <FilterAlcoholStrength v-model:strength="alcoholStrength" />
        <FilterFlavor v-model:filteredFlavors="flavorFilter" />
        <FilterAlcohol v-model:filteredAlcohols="alcoholFilter" />
        <FilterReset
          v-model:filterSweet="tasteFilterSweet"
          v-model:filterSour="tasteFilterSour"
          v-model:filterBitter="tasteFilterBitter"
          v-model:filterStrength="alcoholStrength"
          v-model:filteredFlavors="flavorFilter"
          v-model:filterAlcohols="alcoholFilter"
        />
      </div>
      <MainPageCocktails
        :cocktails-list="cocktails"
        :isMobile="isItMobile"
        :filter-acl="alcoholFilter"
        :filter-bitter="tasteFilterBitter"
        :filter-sour="tasteFilterSour"
        :filter-sweet="tasteFilterSweet"
        :filter-flavor="flavorFilter"
        :filter-strength="alcoholStrength"
      />
    </main>
    <PaginationMianPage
      v-model:page="page"
      :count="countCocktails"
      :perPage="cocktailsPerPage"
      v-if="!isItMobile"
    />
  </div>
</template>

<script>
import FilterTaste from "@/components/FilterTaste.vue";
import FilterReset from "@/components/FilterReset.vue";
import FilterAlcoholStrength from "@/components/FilterAlcoholStrength.vue";
import FilterFlavor from "@/components/FilterFlavor.vue";
import FilterAlcohol from "@/components/FilterAlcohol.vue";
import MainPageCocktails from "@/components/MainPageCocktails.vue";
import PaginationMianPage from "@/components/PaginationMianPage.vue";
import cocktails from "@/data/cocktails";

export default {
  components: {
    MainPageCocktails,
    FilterTaste,
    PaginationMianPage,
    FilterAlcoholStrength,
    FilterReset,
    FilterFlavor,
    FilterAlcohol,
  },
  data() {
    return {
      page: 1,
      cocktailsPerPage: 4,
      tasteFilterSweet: false,
      tasteFilterSour: false,
      tasteFilterBitter: false,
      alcoholStrength: undefined,
      flavorFilter: [],
      alcoholFilter: [],
      filterButton: false,
      isItMobile: false,
    };
  },

  methods: {
    // переключение кнопки фильтрации для мобильных устройств
    changeFilterButton() {
      this.filterButton = !this.filterButton;
    },
    // проверка размера окна
    checkScreenSize() {
      if (window.innerWidth < 550) {
        this.isItMobile = true;
      } else {
        this.isItMobile = false;
      }
    },
    outsideFilterClick(event) {
      const targetElem = this.$refs.filtersBlock;
      const targetButton = this.$refs.buttonForFilter;
      if (!targetElem.contains(event.target) && !targetButton.contains(event.target)) {
        this.filterButton = false;
      }
    },
  },
  computed: {
    //фильтрация списка коктейлей
    filteredCocktails() {
      let filteredCocktails = cocktails;
      //фильтр по сладости
      if (this.tasteFilterSweet) {
        filteredCocktails = filteredCocktails.filter(
          (cock) => cock.taste.sweet >= 40
        );
      }
      //фильтр по кислоте
      if (this.tasteFilterSour) {
        filteredCocktails = filteredCocktails.filter(
          (cock) => cock.taste.sour >= 40
        );
      }
      //фильтр по горечи
      if (this.tasteFilterBitter) {
        filteredCocktails = filteredCocktails.filter(
          (cock) => cock.taste.bitter >= 40
        );
      }
      //фильтр по крепости алкоголя
      if (this.alcoholStrength === 0) {
        filteredCocktails = filteredCocktails.filter(
          (cock) => cock.alcohol === 1
        );
      } else if (this.alcoholStrength < 40) {
        filteredCocktails = filteredCocktails.filter(
          (cock) => cock.alcohol === 2
        );
      } else if (this.alcoholStrength < 75) {
        filteredCocktails = filteredCocktails.filter(
          (cock) => cock.alcohol === 3
        );
      } else if (this.alcoholStrength === undefined) {
        filteredCocktails;
      } else {
        filteredCocktails = filteredCocktails.filter(
          (cock) => cock.alcohol === 4
        );
      }
      //фильтр по вкусу
      if (this.flavorFilter.length != 0) {
        filteredCocktails = filteredCocktails.filter((cock) =>
          cock.flavor.some((elem) => this.flavorFilter.includes(elem))
        );
      }
      //фильтр по виду алкоголя
      if (this.alcoholFilter.length != 0) {
        filteredCocktails = filteredCocktails.filter((cock) =>
          cock.ingredient.some((elem) => this.alcoholFilter.includes(elem))
        );
      }
      return filteredCocktails;
    },
    cocktails() {
      //сортировка для пагинации
      // для мобилбных устройств отсутствует
      if (this.isItMobile == true) {
        return cocktails;
      }
      //разбитие списка коктейлей по cocktailsPerPage на страницу
      let offset = (this.page - 1) * this.cocktailsPerPage;
      return this.filteredCocktails.slice(
        offset,
        offset + this.cocktailsPerPage
      );
    },
    countCocktails() {
      return this.filteredCocktails.length;
    },
  },
  // проверка размера экрана девайса
  created() {
    this.checkScreenSize();
  },
  mounted() {
    document.addEventListener("click", this.outsideFilterClick);
  },
  beforeUnmount() {
    document.removeEventListener("click", this.outsideFilterClick);
  },
};
</script>

