<template>
  <div class="main">
    <div class="picture-block">
      <div class="picture-name">{{ cocktailInfo.title }}</div>
      <div class="picture">
        <img :src="cocktailInfo.image" alt="cocktail picture" />
      </div>
      <div class="group-name">Состав:</div>
      <ul class="cocktail-contents">
        <li
          class="ingredients"
          v-for="cock in cocktailInfo.recipe"
          :key="cock.id"
        >
          <span class="spirit">{{ Object.keys(cock)[0] }}</span>
          <span class="amount">{{ Object.values(cock)[0] }}</span>
        </li>
      </ul>
    </div>
    <div class="description">
      <div class="group-name">Рецепт:</div>
      <ul class="recipe">
        <li class="step" v-for="value in cocktailInfo.steps" :key="value">{{ value }}</li>
      </ul>
      <div class="group-name">Похожие коктейли</div>
      <ul class="look-alike">
        <li v-if="similarFlavor">
          По вкусу
          <router-link
            :to="{ name: 'cocktail', params: { id: similarFlavor.id } }"
            class="look-alike-taste"
            href="#"
          >
            <img :src="similarFlavor.image"
          /></router-link>
          <router-link
            :to="{ name: 'cocktail', params: { id: similarFlavor.id } }"
            class="look-alike-taste-title"
            >{{ similarFlavor.title }}</router-link
          >
        </li>
        <li v-else>
          По вкусу
          <a class="look-alike-taste">
            <img src="img/cocktails img/noCocktail.png"
          /></a>
          <a class="look-alike-taste-title">Нет похожих</a>
        </li>
        <li v-if="similarAlcohol">
          По алкоголю
          <router-link
            :to="{ name: 'cocktail', params: { id: similarAlcohol.id } }"
            class="look-alike-alcohol"
            href="#"
          >
            <img :src="similarAlcohol.image"
          /></router-link>
          <router-link
            :to="{ name: 'cocktail', params: { id: similarAlcohol.id } }"
            class="look-alike-taste-title"
            >{{ similarAlcohol.title }}</router-link
          >
        </li>
        <li v-else>
          По алкоголю
          <a class="look-alike-alcohol" href="#">
            <img src="img/cocktails img/noCocktail.png"
          /></a>
          <a class="look-alike-taste-title">Нет похожих</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import cocktails from "@/data/cocktails";
import alcohol from "@/data/alcohol";

export default {
  computed: {
    //поиск коктейля согласно параметра страницы роутера
    cocktailInfo() {
      return cocktails.find((cock) => cock.id == this.$route.params.id);
    },
    //поиск похожего по вкусу коктейля
    similarFlavor() {
      let otherCocktails = cocktails.filter(
        (cock) => cock.id != this.$route.params.id
      );
      let filteredByFlavor = otherCocktails.filter((cock) =>
        cock.flavor.some((elem) => this.cocktailInfo.flavor.includes(elem))
      );
      return filteredByFlavor[
        Math.floor(Math.random() * filteredByFlavor.length)
      ];
    },
    //поиск похожего по алкоголю коктейля
    similarAlcohol() {
      // отфильтровать из списка всех коктейлей текущий коктейль
      let otherCocktails = cocktails.filter(
        (cock) => cock.id != this.$route.params.id
      );
      // из списка ингридиентов оставить только алкогольные
      let filteredAlcoholList = alcohol.filter((alc) => alc.abvAverage != 0);
      // получаем список id алкогольных ингридиетнов
      let x = [];
      for (let i = 0; i < filteredAlcoholList.length; i++) {
        if (this.cocktailInfo.ingredient.includes(filteredAlcoholList[i].id)) {
          x.push(filteredAlcoholList[i].id);
        }
      }
      // отфильтровать оставшиеся коктейли чтобы пересекались ингридиенты(хоть какие нить)
      let filteredByAlcohol = otherCocktails.filter((cock) =>
        cock.ingredient.some((elem) =>
          this.cocktailInfo.ingredient.includes(elem)
        )
      );
      // отфильтровать оставшиеся коктейли чтобы пересекались алкогольные ингридиенты
      filteredByAlcohol = filteredByAlcohol.filter((cock) =>
        cock.ingredient.some((elem) => x.includes(elem))
      );
      return filteredByAlcohol[
        Math.floor(Math.random() * filteredByAlcohol.length)
      ];
    },
  },
  data() {
    return {};
  },
};
</script>

<style>
.main {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5%;
  margin-top: 5vh;
}
/* START фотка коктейля */
.picture {
  width: 100%;
  max-width: 500px;
  background-color: var(--light-grey);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  border: 2px solid var(--shadow-color);
  backdrop-filter: blur(10px);
}
.picture-block{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.picture img {
  width: 100%;
  max-width: 300px;
  object-fit: cover;
  border-radius: 10px;
}
/* END фотка коктейля */

/* START описание и рецепт */
.description {
  display: flex;
  flex-direction: column;
  gap: 2vh;
  align-items: center;
  max-width: 550px;
}
.cocktail-contents,
.recipe,
.look-alike {
  padding: 1vh;
  border: var(--shadow-color) 2px solid;
  border-radius: 10px;
  background: var(--light-grey);
  backdrop-filter: blur(10px);
  text-align: center;
  width: fit-content;
}
.cocktail-contents{
  width: 100%;
  max-width: 300px;
}

.ingredients:not(:last-child):after {
  display: block;
  font-family: "FontAwesome";
  content: "\2b";
}
.spirit {
  display: inline-block;
  text-align: start;
  width: 80%;
  vertical-align: middle;
  text-align: start;
  padding: 5px 0;
}
.amount {
  display: inline-block;
  text-align: end;
  width: 20%;
  vertical-align: middle;
  padding: 5px 0;
}
.step:not(:last-child):after {
  display: block;
  text-align: center;
  font-family: "FontAwesome";
  content: "\f078";
}
/* END описание и рецепт */
.look-alike {
  display: flex;
  justify-content: space-around;
  gap: 10px;
  width: 30vw;
  height: 20vh;
}

.look-alike li {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.look-alike li img {
  width: 80px;
  height: 120px;
  object-fit: cover;
}
.picture-name {
  font-size: 25px;
  text-align: center;
}
.group-name {
  font-size: 20px;
}

@media screen and (max-width:550px) {
  .look-alike{
    width:100vw;
    height: 25vh;
  }
  .main{
    flex-direction: column;
    margin-top: 0;
  }
  .picture-name{
    margin-top:5%
  }
}
</style>