export default function aboba(filters, viewedArray) {
  if (viewedArray.length == 1) {
    return false;
  }
  let pupa = filters.length - viewedArray.length;
  if (pupa < -1) {
    return false;
  }
  let check = 0;
  viewedArray.forEach((elem) => {
    if (!filters.includes(elem)) {
      check++;
      console.log(check);
    }
  });
  if (check == 1) {
    return true;
  } else {
    return false;
  }
}
