<template>
  <div class="flavor">
    <p>Алкоголь</p>
    <input
      type="text"
      class="searchArea"
      id="input"
      v-model="searchArea"
      placeholder="Поиск"
    />
    <ul>
      <li class="taste-checkbox" v-for="alc in searchAlcohols" :key="alc.id">
        <input
          type="checkbox"
          :id="alc.name"
          :value="alc.id"
          v-model="pushAlcohols"
        />
        <label :for="alc.name" class="container non-alc-filter" :style="{ fontSize: alc.name.length > 14 ? '13px' : '16px' }">
          <i class="fa-solid fa-martini-glass-empty"></i>
          <span
            class="text"
            >{{ alc.name }}</span
          >
        </label>
      </li>
    </ul>
  </div>
</template>

<script>
import alcohol from "@/data/alcohol";

export default {
  props: ["filteredAlcohols"],
  data() {
    return {
      searchArea: "",
    };
  },
  computed: {
    pushAlcohols: {
      get() {
        return this.filteredAlcohols;
      },
      set(value) {
        this.$emit("update:filteredAlcohols", value);
      },
    },
    searchAlcohols() {
      let onlyAlcohol = alcohol.filter((alc) => alc.abvAverage > 0);
      return onlyAlcohol.filter((alc) => {
        return alc.name.toLowerCase().includes(this.searchArea.toLowerCase());
      });
    },
  },
};
</script>

<style>
.searchArea {
  padding: 0 10px;
  border-radius: 10px;
  height: 2rem;
  width: 150px;
  background-color: var(--light-grey);
  color: var(--white-color);
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}
.searchArea::placeholder {
  color: var(--white-color);
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}
</style>