<template>
  <li>
    <input
      class="input-cock"
      placeholder="Поиск коктейля"
      type="text"
      v-model="search"
    />
    <ul class="cock-container active">
      <li class="cock-item" v-for="cock in filteredCocktails" :key="cock.id">
        <router-link class="text-decoration-none text-reset" :to="{name:'cocktail', params:{id: cock.id}}">
          {{ cock.title }}
        </router-link>
      </li>
    </ul>
  </li>
</template>

<script>
import cocktails from "@/data/cocktails";

export default {
  data() {
    return {
      search: "",
      cocktails,
    };
  },
  computed: {
    filteredCocktails() {
      return this.cocktails.filter((cock) =>
        cock.title.toLowerCase().includes(this.search.toLowerCase())
      );
    },
  },
};
</script>

<style>
.input-cock {
  width: 100%;
  height: 100%;
  background-color: transparent;
  border-radius: 10px;
  color: var(--white-color);
  font-size: 16px;
}
.input-cock::placeholder {
  color: antiquewhite;
  text-align: center;
}
.cock-container {
  padding: 0;
  visibility: hidden;
  position: relative;
  z-index: 1;
  background-color: var(--light-grey);
  border-radius: 10px;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  height: 15rem;
  overflow: scroll;
  backdrop-filter: blur(10px);
}
/*.cock-item {
  padding: 1em 0;
}*/
.cock-item a{
  width: 100%;
  height: 100%;
  padding: 1em 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cock-item{
  flex: 25;
}
.cock-item:hover {
  background-color: #5400f080;
  border-radius: 10px;
}
.cock-item:active{
  visibility: visible;
}
.input-cock:focus ~ .active {
  visibility: visible;
}
.inActive {
  visibility: hidden;
}
@media screen and (max-width:550px){
  .input-cock::placeholder{
    font-size: 12px;
  }
}
</style>