<template>
  <div class="taste-filter">
    <p>Тип коктейля</p>
    <ul>
      <li class="taste-checkbox">
        <input type="checkbox" id="flavor1" v-model="sweetFilter">
        <label for="flavor1" class="container">
          <i class="fa-solid fa-martini-glass-empty"></i>
          <span class="text">Сладкий</span>
        </label>
      </li>
      <li class="taste-checkbox">
        <input type="checkbox" id="flavor2" v-model="sourFilter">
        <label for="flavor2" class="container">
          <i class="fa-solid fa-martini-glass-empty"></i>
          <span class="text">Кислый</span>
        </label>
      </li>
      <li class="taste-checkbox">
        <input type="checkbox" id="flavor3" v-model="bitterFilter">
        <label for="flavor3" class="container">
          <i class="fa-solid fa-martini-glass-empty"></i>
          <span class="text">Горький</span>
        </label>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
props: ['sweetCondition', 'sourCondition', 'bitterCondition'],
computed: {
  sweetFilter: {
    get() {
      return this.sweetCondition
    },
    set(value) {
      this.$emit('update:sweetCondition', value)
    },
  },
  sourFilter: {
    get() {
      return this.sourCondition
    },
    set(value) {
      this.$emit('update:sourCondition', value)
    },
  },
  bitterFilter: {
    get() {
      return this.bitterCondition
    },
    set(value) {
      this.$emit('update:bitterCondition', value)
    },
  },
}
}
</script>

