<template>
  <ul class="catalog" v-if="!isMobile">
    <li class="catalog-item" v-for="cock in cocktailsList" :key="cock.id">
      <div class="card">
        <div class="outter">
          <a class="catalog-img">
            <img
              class="catalog-item-image"
              :src="cock.image"
              :alt="cock.title"
              width="250"
              height="400"
            />
          </a>
          <h3 class="catalog-item-title">
            <a>
              {{ cock.title }}
            </a>
          </h3>
        </div>
        <div class="inner">
          <!-- внутренняя сторона карточки коктейля-->
          <p class="cocktail-description">
            Описание:<br />{{ cock.description }}
          </p>
          <ul class="cocktail-content">
            Состав:
            <li class="ingredient" v-for="ingr in cock.recipe" :key="ingr">
              <span class="spirit">{{ Object.keys(ingr)[0] }}</span>
              <span class="amount">{{ Object.values(ingr)[0] }}</span>
            </li>
          </ul>
          <ul class="item-flavor">
            <li
              class="item-flavor-div flavor-sweet"
              :style="{ flex: cock.taste.sweet }"
            >
              <div class="tooltip-container">
                <img src="img/sweet-icon.png" />
                <span class="tooltips">Сладость</span>
              </div>
            </li>
            <li
              class="item-flavor-div flavor-sour"
              :style="{ flex: cock.taste.sour }"
            >
              <div class="tooltip-container">
                <img src="img/sour-icon.png" />
                <span class="tooltips">Кислота</span>
              </div>
            </li>
            <li
              class="item-flavor-div flavor-bitter"
              :style="{ flex: cock.taste.bitter }"
            >
              <div class="tooltip-container">
                <img src="img/bitter-icon.png" />
                <span class="tooltips">Горечь</span>
              </div>
            </li>
          </ul>
          <router-link
            class="link text-decoration-none"
            :to="{ name: 'cocktail', params: { id: cock.id } }"
            >Перейти к коктейлю</router-link
          >
        </div>
      </div>
    </li>
  </ul>
  <div
    id="carouselExampleIndicators"
    class="carousel slide"
    data-ride="carousel"
    v-else
  >
    <ol class="carousel-indicators">
      <li
        data-target="#carouselExampleIndicators"
        :data-slide-to="list.id-1"
        :class="{ active: isFirst(list.id) }"
        v-for="list in cocktailsList"
        :key="list.id"
      ></li>
    </ol>
    <div class="carousel-inner">
      <div
        class="carousel-item"
        id="carousel-inner-id"
        v-for="cock in cocktailsList"
        :key="cock.id"
        :class="{ active: isFirst(cock.id) }"
      >
        <img :src="cock.image" :alt="cock.title" />
        <div class="carousel-caption d-md-block">
          <h5>{{ cock.title }}</h5>
          <p>{{ cock.description }}</p>
        </div>
      </div>
    </div>
    <a
      class="carousel-control-prev"
      href="#carouselExampleIndicators"
      role="button"
      data-slide="prev"
    >
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
      <span class="sr-only">Previous</span>
    </a>
    <a
      class="carousel-control-next"
      href="#carouselExampleIndicators"
      role="button"
      data-slide="next"
    >
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
      <span class="sr-only">Next</span>
    </a>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: [
    "cocktailsList",
    "isMobile",
    "filterSweet",
    "filterSour",
    "filterBitter",
    "filterAcl",
    "filterFlavor",
    "filterStrength",
  ],
  watch: {
    //наблюдатели для корректного отображения карусели bootstarp для мобилок
    filterSour() {
      let findActive = document.getElementsByClassName("active");
      Array.from(findActive).forEach((element) => {
        element.classList.remove("active");
      });
      document.getElementById('carousel-inner-id').classList.add('active')
    },
    filterSweet() {
      let findActive = document.getElementsByClassName("active");
      Array.from(findActive).forEach((element) => {
        element.classList.remove("active");
      });
      document.getElementById('carousel-inner-id').classList.add('active')
    },
    filterBitter() {
      let findActive = document.getElementsByClassName("active");
      Array.from(findActive).forEach((element) => {
        element.classList.remove("active");
      });
      document.getElementById('carousel-inner-id').classList.add('active')
    },
    filterAcl() {
      let findActive = document.getElementsByClassName("active");
      Array.from(findActive).forEach((element) => {
        element.classList.remove("active");
      });
      document.getElementById('carousel-inner-id').classList.add('active')
    },
    filterFlavor() {
      let findActive = document.getElementsByClassName("active");
      Array.from(findActive).forEach((element) => {
        element.classList.remove("active");
      });
      document.getElementById('carousel-inner-id').classList.add('active')
    },
    filterStrength() {
      let findActive = document.getElementsByClassName("active");
      Array.from(findActive).forEach((element) => {
        element.classList.remove("active");
      });
      document.getElementById('carousel-inner-id').classList.add('active')
    },
  },
  computed: {
    //проверка элемента на первую позицию в списке коктейлей для присвоения класса актив для карусели bootstrap
    isFirst() {
      return (positionId) => {
        return this.cocktailsList[0].id == positionId;
      };
    },
  },
};
</script>

<style>
.carousel-item-next,
.carousel-item-prev,
.carousel-item.active {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.carousel-indicators {
  margin: 0 1%;
}
.carousel-caption {
  position: initial;
  padding: 0;
}
.carousel-inner {
  height: 100%;
}
.carousel-item {
  height: 100%;
}
.carousel-item img {
  width: 400px;
}
.tooltip-container > img {
  width: 30px;
}
.link:hover {
  color: var(--main-color);
}
</style>