<template>
  <div class="main-container">
    <div class="popular-explain">В этом списке представлены самые популярные коктейли в мире. Список составлен на основе рейтингов популярных интернет-ресурсов таких как Liqour.com и Difford's Guide</div>
    <ul>
      <li class="cocktail-tile" v-for="cock in getCocktails" :key="cock.id">
        <img class="cocktail-picture" :src="cock.image" />
        <router-link :to="{name:'cocktail', params:{id: cock.id}}" class="cocktail-title" >{{ cock.title }}</router-link>
        <div class="description-popular">{{ cock.description }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
import cocktails from "@/data/cocktails";

export default {
  data() {
    return {
      popularCocktailsId: [12, 13, 14, 15, 11, 16, 8, 17, 18, 1],
      cocktails,
    };
  },
  computed: {
    getCocktails() {
      return cocktails.filter((cock) =>
        this.popularCocktailsId.includes(cock.id)
      );
    },
  },
};
</script>

<style>
.main-container {
  padding: 1vh;
}
.main-container ul {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  padding: 0;
  margin:10px;
}
.cocktail-tile {
  display: flex;
  gap: 10px;
  justify-content: space-around;
  align-items: center;
  font-size: 25px;
  border: var(--shadow-color) 2px solid;
  border-radius: 10px;
  background: var(--light-grey);
  backdrop-filter: blur(10px);
  height: 150px;
}
.cocktail-picture {
  width: 100px;
  height: 150px;
  flex:10;
  object-fit: contain;
}
.description-popular{
  font-size: 20px;
  flex:50;
}
.cocktail-title{
  display: flex;
  flex:15;
  justify-content: center;
  text-align: center;
}
.popular-explain{
  font-size: 25px;
  vertical-align: middle;
  text-align: center;
  margin: 20px 0;
}
@media screen and (max-width:550px){
  .description-popular{
    display: none;
  }
  .popular-explain{
    font-size: 15px;
  }
  .main-container ul{
    justify-content: flex-start;
    height: 75vh;
    overflow: scroll;
  }
}
</style>