<template>
  <div class="calc-main-div">
    <button class="filter-toggler inside-calc-button" @click="changeFilterButton">
      <i class="fa-solid fa-filter">  Открыть список алкоголя</i>
    </button>
    <div class="calc-filter">
      <div
        class="flavor calc-alc-filter"
        :style="{
          visibility: isItMobile
            ? filterButton
              ? 'visible'
              : 'hidden'
            : 'visible',
        }"
      >
        <p>Алкоголь</p>
        <br />
        <input
          type="text"
          class="searchArea filter-search"
          id="input"
          v-model="searchAlc"
          placeholder="Поиск алкоголя"
        />
        <ul class="calc-ul">
          <li class="taste-checkbox" v-for="alc in alcoholList" :key="alc.id">
            <input
              type="checkbox"
              :id="alc.id"
              :value="alc.id"
              v-model="listedAlcohol"
            />
            <label :for="alc.id" class="container non-alc-filter">
              <i class="fa-solid fa-martini-glass-empty"></i>
              <span class="text">{{ alc.name }}</span>
            </label>
          </li>
        </ul>
        <div class="taste-checkbox">
          <input type="button" id="reset" @click="resetAll">
          <label for="reset" class="container reset">
            <i class="fa-solid fa-martini-glass-empty"></i>
            <span class="text">Сбросить<br>фильтр</span>
          </label>
        </div>
      </div>
    </div>
    <!-- блок с подобранными коктейлями -->
    <div class="calc-cocktails-div" v-if="listedAlcohol.length != 0">
      <!-- хватает ингридиентов -->
      <div class="can-do-cocktails">
        <div
          class="wrapper-calc"
          v-for="cock in calcFilteredCocktails"
          :key="cock.id"
        >
          <img class="calc-image" :src="cock.image" :alt="cock.title" />
          <router-link :to="{name:'cocktail', params:{id: cock.id}}">{{ cock.title }}</router-link>
          <!-- колапс бутсрап -->
          <p>
            <button
              class="btn btn-primary"
              type="button"
              data-toggle="collapse"
              :data-target="'#cock' + cock.id"
              aria-expanded="false"
              :aria-controls="'cock' + cock.id"
            >
              Состав
            </button>
          </p>
          <div class="collapse" :id="'cock' + cock.id">
            <div class="card card-body">
              <ul class="calced-ingridients">
                <li class="ingr" v-for="ingr in cock.recipe" :key="ingr">
                  <span class="calc-spirit">{{ Object.keys(ingr)[0] }}</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div class="separator-calc">
        Для коктейлей перечисленных ниже у вас не хватает одного ингридиента
      </div>
      <!-- не хватает одного ингридиента -->
      <div class="need-more-ingr">
        <div class="wrapper-calc" v-for="cock in calc1MoreIngr" :key="cock.id">
          <img class="calc-image" :src="cock.image" :alt="cock.title" />
          <router-link :to="{name:'cocktail', params:{id: cock.id}}">{{ cock.title }}</router-link>
          <p>
            <button
              class="btn btn-primary"
              type="button"
              data-toggle="collapse"
              :data-target="'#cock' + cock.id"
              aria-expanded="false"
              :aria-controls="'cock' + cock.id"
            >
              Состав
            </button>
          </p>
          <div class="collapse" :id="'cock' + cock.id">
            <div class="card card-body">
              <ul class="calced-ingridients">
                <li class="ingr" v-for="ingr in cock.recipe" :key="ingr">
                  <span
                    class="calc-spirit"
                    :class="{
                      notInFilter: cock.alcForRedMark == Object.keys(ingr)[0],
                    }"
                    >{{ Object.keys(ingr)[0] }}</span
                  >
                </li>
              </ul>
            </div>
          </div>
          <!-- <ul class="calced-ingridients">
            <br />
            <li class="ingr" v-for="ingr in cock.recipe" :key="ingr">
              <span class="calc-spirit" :class="{'notInFilter': cock.alcForRedMark==Object.keys(ingr)[0] }">{{ Object.keys(ingr)[0] }}</span>
            </li>
          </ul> -->
        </div>
      </div>
    </div>
    <!-- начальный блок пока не введены данные -->
    <div class="calc-cocktails-div" v-else>
      <div class="wrapper-calc instruction-calc">
        <img
          class="calc-image calc-instruction-image"
          src="img/cocktails img/questionMark.png"
          alt="questionMarkImage"
        />
        <ul class="calced-ingridients instruction-ingridients">
          <h2>Подбор коктейля по имеющимся у вас ингридиентам</h2>
          <br />
          <li class="ingr">
            <span class="calc-spirit"
              >В поле алкоголь отметьте имеющиеся у вас (или планируемые к
              покупке) алкогольные напитки.</span
            ><br />
          </li>
          <li class="ingr">
            <span class="calc-spirit"
              >На месте этой инструкции отобразятся коктейли которые можно
              приготовить имея ТОЛЬКО отмеченный вами алкоголь.</span
            >
          </li>
          <li class="ingr">
            <span class="calc-spirit"
              >При подборе коктейлей не учитываются безалкогольные ингридиенты и
              биттеры.</span
            >
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import alcohol from "@/data/alcohol";
import cocktails from "@/data/cocktails";
import recommendationForCalc from "@/helpers/recommendationForCalc";

export default {
  data() {
    return {
      cocktails,
      searchAlc: "",
      listedAlcohol: [],
      isItMobile: false,
      filterButton: false,
    };
  },
  computed: {
    alcoholList() {
      let filteredAlc = alcohol.filter((cock) => cock.abvAverage !== 0);
      return filteredAlc.filter((alc) =>
        alc.name.toLocaleLowerCase().includes(this.searchAlc.toLowerCase())
      );
    },
    nonAlcohol() {
      let filteredNonAlc = alcohol.filter((cock) => cock.abvAverage === 0);
      return filteredNonAlc.filter((nonAlc) =>
        nonAlc.name
          .toLocaleLowerCase()
          .includes(this.searchNonAlc.toLowerCase())
      );
    },
    calcFilteredCocktails() {
      //составим массив только из алкогольных ингридиентов
      let alcIngr = [];
      for (let i = 0; i < alcohol.length; i++) {
        if (alcohol[i].abvAverage > 0) {
          alcIngr.push(alcohol[i].id);
        }
      }
      //получаем новый массив объектов коктейлей с новым ключ значением содержащим только алкоголь для данного коктейля
      let cockWithAcl = cocktails.map((elem) => {
        const alcValues = elem.ingredient.filter((x) => alcIngr.includes(x));
        return {
          ...elem,
          alc: alcValues,
        };
      });
      //отсортировываем безалкогольные
      cockWithAcl = cockWithAcl.filter((elem) => elem.alc != 0);
      //фильтруем массив коктейлей по ПОЛНОМУ совпадению алкогольных ингридиентов коктейля с фильтрами
      let filteredCock = cockWithAcl;
      if (this.listedAlcohol.length != 0) {
        filteredCock = filteredCock.filter((cock) =>
          cock.alc.every((elem) => this.listedAlcohol.includes(elem))
        );
      }
      return filteredCock;
    },
    calc1MoreIngr() {
      //составим массив только из алкогольных ингридиентов
      let alcIngr = [];
      for (let i = 0; i < alcohol.length; i++) {
        if (alcohol[i].abvAverage > 0) {
          alcIngr.push(alcohol[i].id);
        }
      }
      //получаем новый массив объектов коктейлей с новым ключ значением содержащим только алкоголь для данного коктейля
      let cockWithAcl = cocktails.map((elem) => {
        const alcValues = elem.ingredient.filter((x) => alcIngr.includes(x));
        return {
          ...elem,
          alc: alcValues,
        };
      });
      //отсортировываем безалкогольные
      cockWithAcl = cockWithAcl.filter((elem) => elem.alc != 0);
      //фильтруем массив коктейлей по недостатку ОДНОГО алкогольного ингридиента коктейля с фильтрами
      let filteredCock = cockWithAcl;
      if (this.listedAlcohol.length != 0) {
        filteredCock = filteredCock.filter((cock) =>
          recommendationForCalc(this.listedAlcohol, cock.alc)
        );
      }
      filteredCock = filteredCock.map((elem) => {
        const redId = elem.alc.find(
          (alcNotIncluded) => !this.listedAlcohol.includes(alcNotIncluded)
        );
        let redName = alcohol.find((elem) => elem.id == redId);
        return {
          ...elem,
          alcForRedMark: redName.name,
        };
      });
      return filteredCock;
    },
  },
  methods: {
    resetAll() {
      this.listedAlcohol = []
    },
    checkScreenSize() {
      if (window.innerWidth < 550) {
        this.isItMobile = true;
      } else {
        this.isItMobile = false;
      }
    },
    changeFilterButton() {
      this.filterButton = !this.filterButton;
    },
  },
  created() {
    this.checkScreenSize();
  },
};
</script>

<style>
/*бутстрап кнопка начало*/
.card {
  background-color: transparent;
  border: none;
}
.btn-primary {
  background-color: #4c00f1;
  border: #4c00f1;
}
/*бутстрап кнопка конец*/
.calc-instruction-image {
  margin-bottom: 25px;
}
.notInFilter {
  background-color: #e6181888;
  border-radius: 10px;
  padding: 3px;
}
.separator-calc {
  background: var(--light-grey);
  border-radius: 15px;
  border: 1px solid var(--white-color);
  padding: 5px;
  text-align: center;
}
.calc-alc-filter {
  width: 300px !important;
}
.calc-main-div {
  display: flex;
  gap: 10vw;
  margin-top: 25px;
}
.calc-cocktails-div {
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 90vh;
  overflow: scroll;
  width: 100%;
  align-items: center;
}
.can-do-cocktails,
.need-more-ingr {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}
.calc-filter {
  display: flex;
  flex-direction: column;
}
.non-alc-filter {
  min-height: 2.1em;
  height: fit-content;
}
.text {
  text-align: end;
}
.wrapper-calc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  background: var(--light-grey);
  border-radius: 15px;
  padding: 10px 0;
  width: 200px;
}
.instruction-calc {
  margin-top: 7vh;
  width: 400px;
}
.calced-ingridients {
  padding: 0;
  margin: auto;
  min-width: 200px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.instruction-ingridients {
  width: 350px;
}
.calc-image {
  height: 12vw;
  width: 9vw;
  border: 1px solid var(--white-color);
  border-radius: 10px;
}
.calc-spirit {
  text-align: center;
}
.ingr {
  display: flex;
  justify-content: center;
  width: 80%;
}
.calc-ul {
  height: 50vh !important;
  width: 230px !important;
}
.wrapper-calc h2 {
  font-size: 1.5rem;
  text-align: center;
  height: 2.3em;
  display: flex;
  align-items: center;
}
.filter-search {
  width: 200px !important;
}
@media screen and (max-width: 1430px) {
  .calced-ingridients h2 {
    font-size: 20px;
  }
}
@media screen and (max-width:550px){
  .calc-main-div{
    gap:0
  }
  .calc-alc-filter{
    position: absolute;
    top:16.5%;
    left: 15%;
    background-color:var(--light-grey) ;
    backdrop-filter: blur(10px);
  }
  .calc-cocktails-div{
    margin-top: 15%;
    height: 83vh;
  }
  .calc-image{
    height: 12vh;
    width: 9vh;
  }
  .inside-calc-button{
    width: 100%;
    border-radius: 15px;
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
  }
}
</style>